.title-container {
  display: flex;
  align-items: center;

  .avatar-container {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    margin-top: 1rem;
    .avatar-image {
      width: 200px;
      height: 300px;
      background: lightgray;
      background-image: url("https://scontent.fotp3-3.fna.fbcdn.net/v/t31.18172-8/1398408_10200909696484816_1774043895_o.jpg?_nc_cat=102&ccb=1-5&_nc_sid=174925&_nc_ohc=Tjw2lm1h980AX_1jcsd&_nc_ht=scontent.fotp3-3.fna&oh=da52df738da0f9bf7782555caa636bd0&oe=61896F68");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
    .avatar-side-color {
      width: 150px;
      height: 200px;
      background: #f9ea9a;
    }
  }
  .name-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem;
    h1 {
      font-size: 64px;
      font-weight: 700;
      color: #28262c;
      margin-bottom: 2rem;
      margin-top: 1rem;
      line-height: 76.8px;
      letter-spacing: 7px;
    }
    h4 {
      font-size: 24px;
      font-weight: 400;
      color: #28262c;
      margin-bottom: 1.5rem;
      font-family: "Merriweather", serif;
    }
    hr {
      position: absolute;
      height: 25px;
      border: none;
      background-color: #f9ea9a;
      width: 800px;
      top: 185px;
      opacity: 1;
      transition: all 0.3s ease;
      z-index: -1;
      transform: translateX(-0.7%);
    }
    .skill-container {
      display: flex;
      justify-content: center;
      align-items: center;
      .fab {
        font-size: 32px;
        margin: 1rem;
      }
    }
  }
}

.rainbow-text {
  background: #ca4246;
  background-color: #ca4246;
  background: conic-gradient(
    #ca4246 16.666%,
    #e16541 16.666%,
    #e16541 33.333%,
    #f18f43 33.333%,
    #f18f43 50%,
    #8b9862 50%,
    #8b9862 66.666%,
    #476098 66.666%,
    #476098 83.333%,
    #a7489b 83.333%
  );

  background-size: 57%;
  background-repeat: repeat;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: rainbow-text-animation-rev 0.5s ease forwards;

  cursor: pointer;
}

.rainbow-text:hover {
  animation: rainbow-text-animation 0.5s ease forwards;
}

@keyframes rainbow-text-animation {
  0% {
    background-size: 57%;
    background-position: 0 0;
  }
  20% {
    background-size: 57%;
    background-position: 0 1em;
  }
  100% {
    background-size: 300%;
    background-position: -9em 1em;
  }
}

@keyframes rainbow-text-animation-rev {
  0% {
    background-size: 300%;
    background-position: -9em 1em;
  }
  20% {
    background-size: 57%;
    background-position: 0 1em;
  }
  100% {
    background-size: 57%;
    background-position: 0 0;
  }
}
