.footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 4rem;

  p {
    width: 80%;
  }
  .social-link {
    font-size: 24px;
    margin: 1rem;
    .fab {
      cursor: pointer;
      margin: 0.5rem;
      color: black;
      &:hover {
        opacity: 0.5;
      }
    }
  }
  hr {
    height: 24px;
    border: none;
    float: right;
    background-color: #f9ea9a;
    width: 50px;
    margin-right: 30px;
  }
}
