@import "./App.scss";

.description-container {
  display: flex;
  justify-content: flex-end;
  margin: 5rem;
  .description {
    width: 85%;
    font-family: "Merriweather", serif;
    font-weight: 300;
    line-height: 200%;
    letter-spacing: 0.1em;
    p {
      margin-bottom: 1rem;
    }
    h4 {
      font-size: 20px;
      margin-bottom: 1rem;
    }
    li {
      margin-bottom: 1rem;
      color: gray;
      list-style-type: square;
    }
  }
  .colored-indent {
    width: 26%;
    hr {
      height: 26px;
      border: none;
      float: right;
      background-color: #f9ea9a;
      width: 70px;
      margin-right: 30px;
      margin-top: 8px;
    }
  }
}

.h4 {
  position: relative;
  padding: 10px 10px 10px 0px;
  color: black;
  text-decoration: none;
  font-size: 20px;
  font-family: sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
}
.h4:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -20px;
  width: 100%;
  height: 2px;
  background: #f9ea9a;
  transform-style: linear;
  transition-property: height, width;
  transition-delay: 0.5s, 1s;
  transition-duration: 0.5s;
}
.h4:hover:before {
  transition-property: width, height;
  width: 15px;
  height: 100%;
}
.h4:after {
  content: "";
  position: absolute;
  top: 0;
  right: 5px;
  width: 100%;
  height: 100%;
  background: #f7f4e1;
  transform: scaleX(0);
  transition: 0.5s;
  transition-delay: 0s;
  transform-origin: left;
  z-index: -1;
}
.h4:hover:after {
  transform: scaleX(1);
  transition-delay: 1.5s;
}
