@import "./App.scss";

.work-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(249, 249, 249);
  border-radius: 30px;
  padding: 2rem;
  box-shadow: 0px 5px 10px 0px rgba(4, 4, 4, 0.146);
  .work-description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
    .read-more {
      margin-bottom: 1rem;
      a {
        color: black;
        &:hover {
          color: rgb(83, 83, 51);
        }
      }
    }
    .description {
      font-family: "Merriweather", serif;
      margin-bottom: 1rem;
      word-spacing: 3px;
      letter-spacing: 2px;
    }
    button {
      border: none;
      width: 50%;
      height: 30px;
      border-radius: 10px;
      cursor: pointer;
      background: #f9ea9a;
      transition: all 0.3s ease;
      &:hover {
        background: #ddd086;
        transition: all 0.3s ease;
      }
    }
  }
  .work-name {
    font-family: "Merriweather", serif;
    letter-spacing: 3px;
    text-align: center;
    font-size: 16px;
    padding: 5px;
    margin: 1rem auto;
  }
  .skills-used {
    display: flex;
    justify-content: center;
    align-items: center;
    .fab {
      margin: 1rem;
      font-size: 24px;
    }
  }
  img {
    width: 300px;
    border-radius: 25px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11), 0 2px 2px rgba(0, 0, 0, 0.11),
      0 4px 4px rgba(0, 0, 0, 0.11), 0 6px 8px rgba(0, 0, 0, 0.11),
      0 8px 16px rgba(0, 0, 0, 0.11);
  }
}
