* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

$fontColor: #28262c;

.App {
  font-family: "Lato", sans-serif;
  width: 1440px;
  // background: gray;
  margin: 0 auto;
  height: 100%;
}

.navbar {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  list-style: none;
  margin-right: 5rem;
  a {
    text-decoration: none;
    color: $fontColor;
  }
  li {
    padding: 1rem;
  }
  .home-link {
    font-size: 32px;
    position: relative;
    hr {
      position: absolute;
      height: 10px;
      border: none;
      background-color: #f9ea9a;
      width: 5.5rem;
      top: 2.7rem;
      z-index: -1;
      opacity: 0.5;
      transition: all 0.3s ease;
    }
    &:hover {
      hr {
        opacity: 1;
        transition: all 0.3s ease;
      }
    }
  }
  .work-link {
    font-size: 24px;
  }
  .resume {
    font-size: 24px;
    transition: all 0.6s ease;
    border-radius: 100%;
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &:hover {
      transition: all 0.6s ease;
      background: #f9ea9a;
      &::after {
        opacity: 1;
        transition: all 0.6s ease;
      }
    }
    &::after {
      content: "Download Resume";
      font-size: 12px;
      position: absolute;
      margin-top: 5rem;
      color: gray;
      width: 50px;
      text-align: center;
      opacity: 0;
      transition: all 0.6s ease;
    }
  }
}
